import { faShieldAlt, faKey } from "@fortawesome/pro-light-svg-icons";
import {
  useKeycloak,
  FABackAppIconDefinition,
  FAHarmonyIconDefinition,
  FAFrontAppIconDefinition,
  FAFundManagementAppIconDefinition,
} from "@faharmony/core";
import { IFABarProps } from "../views";

const faApps: IFABarProps["apps"] = [
  {
    id: "fa-back",
    appName: "FA Back",
    icon: FABackAppIconDefinition,
    url: "/app/",
  },
  {
    id: "fa-front",
    appName: "FA Front",
    icon: FAFrontAppIconDefinition,
    url: "/front/",
  },
  {
    id: "fa-fund-app",
    appName: "FA Fund Management",
    icon: FAFundManagementAppIconDefinition,
    url: "/fund/",
  },
  {
    id: "fa-admin-app",
    appName: "FA Admin Console",
    icon: faShieldAlt,
    url: "/admin/",
  },
  {
    id: "fa-auth-console",
    appName: "Keycloak Admin Console",
    icon: faKey,
    url: "/auth/admin/fa/console/#/realms/fa/",
    openInNewWindow: true,
  },
];
const harmonyApp: IFABarProps["apps"] = [
  {
    id: "fa-harmony",
    appName: "FA Harmony",
    icon: FAHarmonyIconDefinition,
    url: "/",
  },
];

const useAppSwitcher = (
  devMode: boolean = false,
  _harmony: boolean = false
): IFABarProps["apps"] => {
  const { keycloakInstance, isAdmin } = useKeycloak();

  // If app is running in devMode
  if (_harmony) return harmonyApp;

  // Return all apps id user is Admin
  if (isAdmin || devMode) return faApps;

  // Return selected apps to in which user has at least one role.
  const clients = Object.entries(keycloakInstance.resourceAccess || {}).map(
    ([resource]) => resource
  );
  const apps: IFABarProps["apps"] = [];
  faApps.forEach((app) => {
    if (clients.some((client) => client === app.id)) apps.push(app);
  });
  return apps;
};

export default useAppSwitcher;
