import React from "react";
import styled from "@emotion/styled";
import {
  Tooltip,
  Theme,
  Icon,
  FALogoOpaqueIconDefinition,
} from "@faharmony/core";

const StyledLogo = styled.div`
  margin: 0;
  width: 100%;
  height: 100%;
  background-color: transparent;
  text-align: center;
  color: ${() => Theme.colorTint()};
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 36px;

  .faLogo {
    transform: translateY(${Theme.props.spacing.sm});
    --fa-primary-color: #ffffff;
    --fa-primary-opacity: 1;
    --fa-secondary-color: #ae001a;
    --fa-secondary-opacity: 1;
  }
`;

const FALogo = () => {
  return (
    <StyledLogo onClick={() => window.location.reload()}>
      <Tooltip value="FA Platform">
        <div className="faLogo">
          <Icon size="1x" icon={FALogoOpaqueIconDefinition} />
        </div>
      </Tooltip>
    </StyledLogo>
  );
};

export default FALogo;
